import React, { Component } from "react";
import { adresse } from "../../config";
import { Link } from "react-router-dom";
import classes from "../../styles/books.module.scss";
import axios from "../../axiosInstance";

class Sample extends Component {
  state = {
    id: null,
    sample: "",
  };
  componentDidMount() {
    let { id } = this.props.match.params;
    this.setState(
      {
        id: id,
      },
      () => {
        const response = axios.get(`${adresse}/books/sample/${this.state.id}`);
        this.setState({ sample: response.data });
      }
    );
  }
  render() {
    return (
      <div>
        <div>
          {this.state.sample
            ? this.state.sample
            : "Autor nie dodał jeszcze próbki swojej książki"}
        </div>
        <Link to={`/${this.state.id}/reading/${0}`}>
          <button className={classes.btnChaptitle}>Czytaj dalej</button>
        </Link>
      </div>
    );
  }
}
export default Sample;
